import "./import/modules";

let menuButton = document.querySelector(".button__menu");
let closeButton = document.querySelector(".burger-menu-button");
let burgerMenu = document.querySelector(".burger-menu");

menuButton.onclick = function () {
    burgerMenu.classList.add("active");
};
closeButton.onclick = function () {
    burgerMenu.classList.remove("active");
};

function runningNumbers(num, elem, step, time){
    let element = document.querySelector('.' + elem);
    let n = 0;
    let t = Math.round(time/(num/step));
    let interval = setInterval(() => {
        n = n + step;
        if(n >= num) {
            clearInterval(interval);
            n = num;
        }
        element.innerHTML = n;
    }, t);
}

const counter = document.querySelector('.counter-block');
const counterTop = counter.getBoundingClientRect().top + window.pageYOffset;
const counterBottom = counter.getBoundingClientRect().bottom + window.pageYOffset;
const counterHeight = counterBottom - counterTop;

window.addEventListener('scroll', function onScroll() {
    if(window.pageYOffset > counterTop - window.innerHeight + counterHeight) {
        this.removeEventListener('scroll', onScroll);
        runningNumbers(12, "counter-block__published", 2, 700);
        runningNumbers(5, "counter-block__bestsellers", 1, 500);
        runningNumbers(15000, "counter-block__readers", 367, 1600);
    }
});

const anchor = document.querySelector('.arrow-up');
const mainPage = document.querySelector('.main-page');
const mainPageBottom = mainPage.getBoundingClientRect().bottom + window.pageYOffset;

window.addEventListener('scroll', function () {
    if(window.pageYOffset < mainPageBottom) anchor.style.opacity = '0';
    else {
        anchor.style.opacity = '0.6';
        anchor.style.visibility = 'visible';
    }
});

anchor.addEventListener("click", function(e) {
    e.preventDefault() 
    const goto = anchor.hasAttribute('href') ? anchor.getAttribute('href') : 'body'
    document.querySelector(goto).scrollIntoView({
      behavior: "smooth",
      block: "start"
    })
});

anchor.addEventListener("mouseover", function(e) {
    anchor.style.opacity = '1';
});

anchor.addEventListener("mouseout", function(e) {
    anchor.style.opacity = '0.6';
});


const aboutBlockButton = document.querySelector(".about-block-table__button");
const nextItems = document.querySelector(".about-block-table__list_next");

aboutBlockButton.onclick= function() {
    if(!aboutBlockButton.classList.contains('close')){
        nextItems.style.opacity = '1';
        nextItems.style.visibility = 'visible';
        aboutBlockButton.innerHTML = 'Close';
        aboutBlockButton.style.bottom = '-65px';
        aboutBlockButton.classList.add('close');
    } else {
        nextItems.style.opacity = '0';
        aboutBlockButton.innerHTML = 'More';
        aboutBlockButton.style.bottom = '115px';
        aboutBlockButton.classList.remove('close');
    }
}




